<script>
import axios from 'axios';
// import Layout from '@layouts/default'
import appConfig from '@src/app.config'
import Utils from '../../../../../utils/Util'

/**
 * Login component
 */
export default {
	page: {
		title: 'Log in',
		meta: [{ name: 'Halaman', content: `Log in to ${appConfig.title}` }],
	},
	// components: { Layout },
	data() {
		return {
			api: Utils.Host(),
			username: '',
			password: '',
			authError: null,
			tryingToLogIn: false,
			isAuthError: false,
			alert:false
		}
	},
	methods: {
		async Login() {
		try {
			const akun = new URLSearchParams();
			akun.append("username", this.username);
			akun.append("password", this.password);
			let response = await axios.post(this.api+"/login", akun);
			if (response.data.Error_code === 0 || response.data.Error_code === '0'){
			let token = response.data.Token;
			localStorage.setItem("jwt", token);
			if (token) {
				this.$router.push("dashboard");
			}
			} else {
				this.alert = true
				this.message = response.data.Error_desc
			}
		} catch (err) {
			this.alert = true
			this.message = err.response
		}
		},
	},
}
</script>

<template>
	<Layout>
		<!-- end row -->
		<div class="account-pages my-5">
			<div class="container">
				<div class="row justify-content-center">
					<div class="col-xl-10">
						<div class="card">
							<div class="card-body p-0">
								<div class="row">
									<div class="col-md-6 p-5">
										<div class="mx-auto mb-5">
											<a routerLink="/">
												<h3 class="d-inline align-middle ml-1 text-logo"
													>Login Administrator</h3
												>
											</a>
										</div>

										<b-alert
											v-model="alert"
											variant="danger"
											dismissible
											>{{ message }}</b-alert
										>

									
											<div class="form-group">
												<label class="form-control-label">Username</label>
												<div class="input-group input-group-merge">
													<div class="input-group-prepend">
														<span class="input-group-text">
															<feather
																type="mail"
																class="align-middle icon-dual"
															></feather>
														</span>
													</div>
													<b-form-input
														id="input-1"
														v-model="username"
														type="text"
														required
														placeholder="Enter username"
													></b-form-input>
												</div>
											</div>
											<div class="form-group mt-4">
												<label class="form-control-label">Password</label>
												<div class="input-group input-group-merge">
													<div class="input-group-prepend">
														<span class="input-group-text">
															<feather
																type="lock"
																class="align-middle icon-dual"
															></feather>
														</span>
													</div>
													<b-form-input
														id="password"
														v-model="password"
														type="password"
														required
														placeholder="Enter your password"
													></b-form-input>
												</div>
											</div>
											<b-form-group id="button-group" class="mt-4 mb-1">
												<b-button
													type="submit"
													variant="primary"
													class="btn-block"
													@click="Login"
													>Log In</b-button
												>
											</b-form-group>
									</div>


									<div class="col-lg-6 d-none d-md-inline-block">
										<img src="@assets/images/banner-login.jpg" width="100%" height="100%" />
									</div>


								</div>
							</div>
							<!-- end card-body -->
						</div>
						<!-- end card -->

					</div>
					<!-- end col -->
				</div>
				<!-- end row -->
			</div>
			<!-- end container -->
		</div>
	</Layout>
</template>

